.List {
    display: flex;
    justify-content: center;
}
.ToDoList {
    width: 300px;
    display: flex;
    flex-direction: column ;
    align-items: flex-start;
    margin: 10px;
    padding: 10px;
    border: 1px solid;
    border-radius: 3px;
}
.do {
    width: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    list-style-type: none;
}
.doings {
    width: 100%;
    margin: 0;
    padding: 0;
}
.complit {
    max-width: 12px;
}
img {
    max-width: 12px;
}